/* Anmation */
:root {
 --speed: 500ms; 
  }

  .underline{
    position: absolute;
    border-bottom:4px solid pink;
    width: 50%;
    display: flex;
    overflow: hidden;
    bottom: 0%;

}

.underline-enter{
  margin-left: 50%;
}

.underline-enter-active{
  margin-left: 0%;
  transition: margin-left 500ms ease;
  transition: margin-left var(--speed) ease;
}

.underline-exit-active{
  margin-left: 50%;
  transition: margin-left 500ms ease;
  transition: margin-left var(--speed) ease;

}


.underline2{
  position: absolute;
  border-bottom:4px solid pink;
  width: 50%;
  display: flex;
  overflow: hidden;
  bottom: 0%;
  right: 0%;
  
}

.underline2-enter{
  margin-right: 50%;
}

.underline2-enter-active{
  margin-right: 0%;
  transition: margin-right 500ms ease;
  transition: margin-right var(--speed) ease;
}

.underline2-exit-active{
  margin-right: 50%;
  transition: margin-right 500ms ease;
  transition: margin-right var(--speed) ease;

}

.slideAnimation-enter {
    position: absolute;
    transform: translateX(-260%);
    overflow: hidden;
    opacity: 0;
  }
  .slideAnimation-enter-active {
    transform: translateX(0%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
    opacity: 1;

  }
  .slideAnimation-exit {
    position: absolute;
  }
  .slideAnimation-exit-active {
    transform: translateX(-260%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
    opacity: 0;
  }
  
  
  .slideAnimation2-enter {
    position: absolute;
    transform: translateX(260%);
    opacity: 0;
    
  }
  .slideAnimation2-enter-active {
    transform: translateX(0%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
    opacity: 1;
   
  }
  .slideAnimation2-exit {
    position: absolute;
  }
  .slideAnimation2-exit-active {
    transform: translateX(260%);
    transition: all 500ms ease;
    transition: all var(--speed) ease;
    opacity: 0;
    position: absolute;
  }
  /* Anmation */
